.header {
  background: #393d45;
  color: #fff;
}

.header__wrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.top-nav {
  margin-bottom: 0;
}

.top-nav__item {
  display: inline-block;
  position: relative;
}

.top-nav__item:after {
  content: '';
  display: block;
  position: absolute;
  top: 20px;
  right: -2px;
  width: 1px;
  bottom: 20px;
  background: url("../img/nav-separator.png") no-repeat;
}

.top-nav__item:last-child:after {
  display: none;
}

.top-nav__link {
  font-weight: 100;
  color: rgba(255, 255, 255, 0.7);
  display: block;
  padding: 20px 30px;
  border-bottom: 3px solid rgba(66, 232, 171, 0);
  transition: .2s;
}

.top-nav__link:hover, .top-nav__link:focus, .top-nav__link:visited {
  cursor: pointer;
  color: #fff;
  text-decoration: none;
  border-color: #42e8ab;
}

.lang-nav {
  display: inline;
  margin-bottom: 0;
  margin-right: -4px;
  position: relative;
}

.lang-nav__item {
  display: none;
  list-style: none;
}

.lang-nav__link {
  display: block;
  padding: 21px 10px;
  color: rgba(255, 255, 255, 0.7);
  background: transparent;
  transition: .2s;
  font-weight: 100;
}

.lang-nav__link:hover, .lang-nav__link:focus, .lang-nav__link:visited {
  text-decoration: none;
  color: #fff;
  background: #30333a;
}

.lang-nav__image {
  position: relative;
  top: -1px;
  margin-right: 5px;
}

.lang-nav .active {
  display: inline-block;
}

.cabinet {
  color: rgba(255, 255, 255, 0.7);
  font-weight: 100;
  padding: 22px 10px;
}

.cabinet__image {
  position: relative;
  top: -1px;
  margin-right: 5px;
}

.cabinet:hover {
  color: #fff;
  text-decoration: none;
  background: #30333a;
}

.s1 {
  background: rgba(57, 61, 69, 0.9);
  padding: 40px 0 20px;
}

.s1__title {
  color: #fff;
  margin: 20px 0 10px;
  font-size: 30px;
  font-weight: 700;
  text-align: center;
}

.s1__desc {
  color: #fff;
  font-size: 18px;
  font-weight: 100;
  text-align: center;
}

.s1__button {
  padding: 15px 30px;
  background: #d4495a;
  color: #fff;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 700;
  border: none;
  outline: none;
  transition: .2s;
}

.s1__button:hover, .s1__button:active, .s1__button:focus {
  background: #bd2d3e;
  text-decoration: none;
  color: #fff;
}

.s1__button__image {
  position: relative;
  top: -2px;
  margin-left: 10px;
}

.form__wrapper {
  margin-top: 30px;
  display: flex;
  justify-content: center;
}

.s1-form {
  display: flex;
  margin-right: 40px;
}

.s1-form__input {
  border: none;
  width: 250px;
  padding: 9px 20px;
}

.s1-form__button {
  border: none;
  width: 56px;
  background: #42e8ab;
  transition: .2s;
}

.s1-form__button:hover {
  background: #1bdc95;
}

.s1-form__input:hover, .s1-form__input:focus, .s1-form__button:hover, .s1-form__button:focus {
  outline: none;
}

.middle-nav {
  margin-top: 20px;
  margin-bottom: 0;
  padding-left: 0;
  text-align: center;
}

.middle-nav__item {
  list-style: none;
  display: inline-block;
}

.middle-nav__link {
  color: #fff;
  padding: 40px;
  font-weight: 100;
  display: block;
  filter: grayscale(1);
  transition: .2s;
}

.middle-nav__link:hover, .middle-nav__link:focus {
  background: rgba(57, 61, 69, 0.2);
  color: #42e8ab;
  text-decoration: none;
  filter: grayscale(0);
}

.middle-nav__image {
  position: relative;
  margin-top: -3px;
  margin-right: 5px;
}

.s2 {
  background: #ccd1dd;
}

.s2__wrapper {
  display: flex;
  justify-content: center;
}

.s2__link {
  color: #393d45;
  display: block;
  position: relative;
  padding: 20px 40px 16px;
  transition: .2s;
  opacity: .5;
  border-bottom: 4px solid transparent;
}

.s2__link:hover {
  color: #393d45;
  text-decoration: none;
  opacity: 1;
  border-color: #393d45;
}

.s2 .active {
  color: #393d45;
  text-decoration: none;
  opacity: 1;
  border-color: #393d45;
}

.s2__image {
  position: relative;
  margin-top: -2px;
  margin-right: 5px;
}

.s3 {
  padding: 30px 0;
}

.s3 .container-fluid {
  max-width: 1490px;
}

.s3__button {
  padding: 15px 30px;
  background: #d4495a;
  color: #fff;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 700;
  border: none;
