}

.s3__button:hover, .s3__button:active, .s3__button:focus {
  background: #bd2d3e;
  text-decoration: none;
  color: #fff;
}

.product-cart {
  margin-bottom: 30px;
}

.product-cart__image {
  height: 350px;
  background-size: cover;
  background-position: top center;
}

.product-cart__body {
  background: #818897;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.product-cart__body-left, .product-cart__body-right {
  padding: 7px 10px;
}

.product-cart__body-left {
  width: 60%;
}

.product-cart__body-right {
  width: 40%;
  text-align: right;
  position: relative;
  /****** Style Star Rating Widget *****/
  /***** CSS Magic to Highlight Stars on Hover *****/
}

.product-cart__body-right label {
  margin-bottom: 0;
}

.product-cart__body-right .rating {
  border: none;
}

.product-cart__body-right .rating > input {
  display: none;
}

.product-cart__body-right .rating > label:before {
  margin-left: 5px;
  font-size: 16px;
  font-family: FontAwesome;
  display: inline-block;
  content: "\f005";
}

.product-cart__body-right .rating > .half:before {
  content: "\f089";
  position: absolute;
}

.product-cart__body-right .rating > label {
  color: #ddd;
  float: right;
}

.product-cart__body-right .rating > input:checked ~ label {
  color: #FFD700;
}

.product-cart__title {
  color: #fff;
  margin: 0;
  font-size: 18px;
  font-weight: 700;
}

.product-cart__footer {
  background: #555b67;
  display: flex;
  align-items: center;
  color: #fff;
}

.product-cart__footer-left, .product-cart__footer-right {
  padding: 7px 10px;
}

.product-cart__footer-left {
  width: 60%;
}

.product-cart__footer-left .row:first-child {
  margin-bottom: 8px;
  padding: 3px 0;
}

.product-cart__footer-right {
  width: 40%;
  font-size: 18px;
  font-weight: 700;
  text-align: right;
}

.product-cart__favorite {
  position: relative;
  display: inline-block;
  top: -2px;
  filter: grayscale(1) brightness(4);
  transition: .2s;
}

.product-cart__favorite img {
  margin-right: 5px;
}

.product-cart__favorite:hover {
  filter: grayscale(0);
  transform: scale(1.2);
}

.product-cart__icon {
  position: relative;
  margin-top: -2px;
  margin-right: 5px;
}

.product-cart__favorite.active {
  filter: grayscale(0);
}

