}

.s8__items-wrapper {
  position: relative;
}

.s8__item {
  text-align: center;
}

.s8__image {
  width: 135px;
  height: 135px;
  background-position: center center;
  background-size: cover;
  border-radius: 50%;
  position: relative;
  margin: 0 auto;
}

.s8__name {
  margin-bottom: 20px;
  font-size: 24px;
  color: #393d45;
  font-weight: 700;
}

.s8__text {
  font-size: 14px;
  color: rgba(57, 61, 69, 0.8);
  line-height: 20px;
  font-weight: 100;
}

.s8__left, .s8__right {
  width: 55px;
  height: 55px;
  background: #ccd1dd;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: .2s;
  position: absolute;
}

.s8__left:hover, .s8__right:hover {
  background: #393d45;
}

.s8__left {
  left: 0;
  top: 50%;
  transform: translate3d(-140%, 100%, 0);
}

.s8__right {
  right: 0;
  top: 50%;
  transform: translate3d(140%, 100%, 0);
}

